.appWrapper {
    display: grid;
    grid-template-rows: 60px 1fr;
    grid-template-columns: 2fr 10fr;
    grid-gap: 10px;
    grid-template-areas: 'header header'
                         'nav content' ;
}
.appWrapperContent {
    grid-area: content;
    background-color: aqua;
    display: flex;
    flex-direction: column;
}


